import { useState } from 'react';

import { useTranslation } from "react-i18next";

import Types from './Types';
import CheckBox from './CheckBox';
import Button from './Button';
import State from './State';

import styles from './index.module.scss';

const CustomInput = ({
    customClass,
    langCodePlaceholder,
    inputType,
    configCheck,
    configButton,
    returnValue = () => {}
}) => {

    const { t } = useTranslation(null, { keyPrefix: "input" });

    const def = {
        fill: false,
        bad: false,
        good: false
    };

    const [classObj, setClassObj] = useState(def);

    const handleChange = (event) => {
        const { value } = event.target;
        const pattern = event.target.getAttribute('data-pattern');

        if (value === "") {
            returnValue(false);

            return setClassObj(def);
        }

        setClassObj(prevState => ({
            ...prevState,
            'fill': styles.fill
        }));

        if (!pattern) return returnValue(value);

        const [text, good, bad] = new RegExp(pattern).test(value) ? [
            value,
            styles.good,
            false
        ] : [
            false,
            false,
            styles.bad
        ];

        returnValue(text);

        setClassObj(prevState => ({
            ...prevState,
            'good': good,
            'bad': bad,
        }));
    };

    const handlerCustomButton = () => {
        typeof configButton.callback === "function" && configButton.callback()
    }

    let classNameArr = Object.values(
        Object.assign(classObj, {
            chek: configCheck ? styles.checked : '',
            cust: customClass ? customClass : '',
            inp: styles.input
        })
    ).filter(function (v) {
        return !!v;
    });

    const { svg, type, pattern } = Types[inputType];

    return (
        <>
            <div className={classNameArr.join(' ')}>

                {svg && <div className={styles.icon}>{svg}</div>
                }

                <input
                    type={type || "text"}
                    placeholder={t(langCodePlaceholder)}
                    onChange={handleChange}
                    data-pattern={pattern || ''}
                />

                {
                    (classObj.bad && <State stateType="bad" />)
                    || (classObj.good && <State stateType="good" />) || ""
                }

                {
                    configButton && <Button
                        classDisabled={configButton.disabled ? true : false}
                        onClick={handlerCustomButton}
                    >
                        {configButton.svg || ''}
                        {configButton.text || ''}
                    </Button>
                }
            </div>

            {
                configCheck && <CheckBox
                    stateBox={configCheck.checked}
                    stateInput={classObj.good}
                    boxText={configCheck.text}
                    onClick={
                        () => configCheck.callback(!configCheck.checked)
                    }
                >
                    {configCheck.text}
                </CheckBox>
            }
        </>
    );
};

export default CustomInput;