import { Notification } from '../Notification';
import { sessionKeys, changeTwofa } from '../User/session';

const prepeareCallback = (callback, data) => {
    if (data.success === false) {
        if (callback.error !== null) callback.error(data);

        onCode(data);

        onUnlogin(data);

        onSessionTwoFa(data);

    } else {
        if (callback.success !== null) callback.success(data);
    }
};

const onCode = (data) => {
    if (!data.code) return;

    let varia = false;

    if (data.input)
        varia = { valueEmpty: `'${data.input}'` };

    if (data.wait)
        varia = { valueZero: data.wait };

    Notification.error(
        data.code,
        varia
    );
}

const onSessionTwoFa = (data) => {
    if (!data.sessionTwoFa) return;

    changeTwofa.enable();
};

const onUnlogin = (data) => {
    if (!data.unlogin) return;

    sessionKeys.clear(true);
};

export default prepeareCallback;