import { useState } from 'react';
import { useTranslation } from "react-i18next";

import CustomInput from '../CustomInput';
import PhoneInput from '../CustomInput/PhoneInput';
import Form from '../Modal/Form';
import Button from '../CustomButton';
import Buttons from '../CustomButton/Buttons';
import TopSwitch from './TopSwitch';

import { Notification } from '../Notification';
import { stringToSha256 } from '../Util/Sha256';
import { sessionKeys } from '../User/session';
import API from '../API';

import { svgAsset } from '../svgAsset';

const BlockReg = ({ changeType }) => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });

    const [SwitchType, setSwitchType] = useState('Email');
    const [isNoNeedPassword, setIsNoNeedPassword] = useState(true);

    const [emailValue, setEmailValue] = useState(false);
    const [passwordValue, setPasswordValue] = useState(false);

    const [iddValue, setIddValue] = useState(false);
    const [phoneValue, setPhoneValue] = useState(false);
    const [codeValue, setCodeValue] = useState(false);

    const setPhonePart = (type, data) => type === "idd" ? setIddValue(data) : setPhoneValue(data);
    const toggleReg = () => SwitchType === "Email" ? handlerRegEmail() : handlerRegPhone();

    const handlerSuccess = (data) => {
        sessionKeys.add(data['auth']);

        changeType(false);
    }

    const handlerRegPhone = () => {
        if (!phoneValue || !codeValue || !iddValue)
            return Notification.error("error.FillAllData");

        API.send(
            "reg/create/phone",
            {
                "phone": `${iddValue}${phoneValue}`,
                "code": codeValue
            },
            handlerSuccess
        );
    }

    const handlerRegEmail = () => {
        if (!emailValue || (!passwordValue && !isNoNeedPassword))
            return Notification.error("error.FillAllData");

        API.send(
            "reg/create/email",
            {
                "email": emailValue,
                "password": (
                    isNoNeedPassword ?
                        "passwordUnset"
                        : stringToSha256(passwordValue)
                )
            },
            handlerSuccess
        );
    }

    const blocks = {
        Email: <>
            <CustomInput
                inputType="email"
                langCodePlaceholder="Email"
                returnValue={setEmailValue}
                configCheck={
                    {
                        text: t('Send password on email'),
                        callback: setIsNoNeedPassword,
                        checked: isNoNeedPassword
                    }
                }
            />

            {!isNoNeedPassword ? (
                <CustomInput
                    returnValue={setPasswordValue}
                    inputType="password"
                    langCodePlaceholder="Password"
                />
            ) : ''}
        </>,
        Phone: <PhoneInput
            returnPhone={setPhonePart}
            returnCode={setCodeValue}
        />
    };

    return (
        <>
            <Form
                codeLangTitle="Reg"
                beforeBox={<TopSwitch switchType={SwitchType} onClickTab={setSwitchType} />}
                textFill={true}
            >
                {blocks[SwitchType]}

                <CustomInput inputType="promocode" langCodePlaceholder="Promocode" />

                <Buttons>
                    <Button onClick={() => changeType('Auth')}>
                        {svgAsset.User}
                        {t('Sign In')}
                    </Button>

                    <Button isGreen="true" onClick={toggleReg}>
                        {svgAsset.Key}
                        {t('Create an account')}
                    </Button>
                </Buttons>
            </Form>
        </>
    );
}

export default BlockReg;