import { useTranslation } from "react-i18next";

import styles from './Form.module.scss';

const Wrapper = ({ 
        classForm = "", 
        classBox = "", 
        codeLangTitle = "", 
        textFill = false, 
        beforeBox = "", 
        svgTitle = "", 
        children 
    }) => {
        
    const { t } = useTranslation(null, { keyPrefix: "modal" });

    return (

        <div className={`${styles.form} ${classForm}`}>
            <div className={styles.title}>
                {svgTitle}
                {t('title.' + codeLangTitle)}
            </div>

            {beforeBox}

            <div className={`${styles.box} ${classBox}`}>
                {
                    textFill && <div className={styles.fillDetails}>
                        {
                            typeof textFill === 'string' ?
                                t(textFill)
                                :
                                t('Fill in the details')
                        }
                    </div>
                }

                {children}
            </div>
        </div>
    );
}

export default Wrapper;