import i18n from 'i18next';

import API from '../API';
import Twofactor from "../store/twofactor";
import storeUser from "../store/user";
import { AUTH_SECRET } from "../SETTINGS";
import {
    encodeBlowfish,
    decodeBlowfish,
} from "../Util/Blowfish";

const checkSession = () => {
    if (!localStorage.getItem('W-Auth'))
        return sessionKeys.clear();

    API.sendAuth("session/information", {}, ({information}) => {
        storeUser.setSession(information);

        changeTwofa.disable();

        if (information.language !== localStorage.getItem('W-Language'))
            i18n.changeLanguage(information.language);
    });
};

const changeTwofa = (state) => Twofactor.setNeed(state);

changeTwofa.enable = () => changeTwofa(true);

changeTwofa.disable = () => changeTwofa(false);

const sessionKeys = (obj) => {
    if (obj.type === 'add')
        sessionKeys.set(obj.auth + ":" + Math.random())

    checkSession();
};

sessionKeys.add = (auth) => {
    sessionKeys({ type: "add", auth: auth })
};

sessionKeys.update = () => {
    const auth = sessionKeys.read();

    sessionKeys({ type: "add", auth: auth })
};

sessionKeys.check = (wait = 1) => setTimeout(() => sessionKeys({ type: "check" }), wait);

sessionKeys.clear = (isNeedUpdate = false) => {
    storeUser.clearSession();

    changeTwofa.disable();

    if (isNeedUpdate)
        localStorage.removeItem('W-Auth');
};

sessionKeys.read = () => {
    const wAuth = localStorage.getItem('W-Auth');

    if (!wAuth) return null;

    const [id, token] = decodeBlowfish(AUTH_SECRET, wAuth).split(":");

    return id + ":" + token;
}

sessionKeys.set = (newAuth) => {
    const bufferHex = encodeBlowfish(AUTH_SECRET, newAuth);

    localStorage.setItem('W-Auth', bufferHex)
}

export {
    sessionKeys,
    changeTwofa
};