import { makeAutoObservable } from "mobx"

class storeUser {
    isAuth = false;
    arrBalance = {};

    constructor() {
        makeAutoObservable(this)
    }

    setSession({balance}) {
        this.isAuth = true;

        this.arrBalance = balance;
        //Получаем инфу вносим isLogin, userInfo, need2fa modal в site, need add balance и т.д.
    }

    clearSession() {
        //Очищаем isLogin, userInfo = {}, need add balance и т.д.
        this.isAuth = false;
        this.arrBalance = {};
    }

    countBalance() {
        return this.arrBalance ? Object.keys(this.arrBalance).length : -1;
    }
}

export default new storeUser();