import { useState } from 'react';

import { useTranslation } from "react-i18next";

import CustomInput from '../CustomInput';
import Form from '../Modal/Form';
import Button from '../CustomButton';
import Buttons from '../CustomButton/Buttons';
import { Notification } from '../Notification';
import API from '../API';

import { svgAsset } from '../svgAsset';

const BlockReset = ({ changeType }) => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });

    const [emailValue, setEmailValue] = useState(false);

    const handlerReset = () => {
        if (!emailValue) {
            return Notification.error("error.FillAllData");
        }

        API.send(
            "resert/request",
            {
                email: emailValue
            },
            () => changeType('ResetSuccess')
        );
    };

    return (
        <>
            <Form
                codeLangTitle="Reset"
                textFill={true}
            >
                <CustomInput
                    inputType="email"
                    langCodePlaceholder="Email"
                    returnValue={setEmailValue}
                />

                <Buttons>
                    <Button onClick={() => changeType('Auth')}>
                        {svgAsset.User}
                        {t('Sign In')}
                    </Button>

                    <Button isGreen="true" onClick={handlerReset}>
                        {svgAsset.Key}
                        {t('Reset password')}
                    </Button>
                </Buttons>
            </Form>
        </>
    );
}

export default BlockReset;