import { useTranslation } from "react-i18next";

import Form from '../Modal/Form';
import Button from '../CustomButton';

import { svgAsset } from '../svgAsset';

import styles from './BlockResetSuccess.module.scss';

const BlockResetSuccess = ({ changeType }) => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });

    return (
        <>
            <Form
                codeLangTitle="ResetSuccess"
            >

                <div className={styles.box}>
                    {svgAsset.Tick}
                    
                    <div className={styles.text}>{t('Reset instructions have been sent to your email')}</div>
                </div>

                <Button isBig={true} onClick={() => changeType('Auth')}>
                    {svgAsset.User}
                    {t('Back to login')}
                </Button>
            </Form>
        </>
    );
}

export default BlockResetSuccess;