import styles from './Points.module.scss';

const Points = ({ activePoint, count, changeOnClick }) => {
    const arrayCount = [...Array(count).keys()].map(x => x + 1);

    return <div className={styles.points}>
        {
            arrayCount.map((numPos) => {
                return (<div
                    key={`point-${numPos}`}
                    className={`${styles.point} ${
                        (activePoint === numPos && styles.active)
                        || ''
                    }`}
                    onClick={() => changeOnClick(numPos)}
                />)
            })
        }
    </div>
}

export default Points;