import ContentBox from '../ContentBox';
import TableRow from '../TableRow';
import { svgAsset, svgMenu, svgCryptoPayments } from '../svgAsset';

import styles from './index.module.scss';
import { useEffect, useState } from 'react';

const LastBet = () => {
    const headTable = [
        { langCode: "Game" },
        { langCode: "Player Name" },
        { langCode: "Bet ID" },
        { langCode: "Payout"},
        { langCode: "Profit" }
    ];

    const [arrayBets, setArrayBets] = useState([
        [
            { style: "grey", svg: svgMenu.Crash, langCode: "Crash" },
            { style: "white", text: "Archibaldo" },
            { style: "grey", text: "1723406388615713031" },
            { style: "white", text: "2.25 x" },
            { style: "green", svg: svgCryptoPayments["btc"], text: "+210.00000" }
        ]
    ]);

    useEffect(() => {
        const TODODELETETHIS = setInterval(() => {
            let list = arrayBets;

            if (list.length === 7)
                list.pop();
            
            setArrayBets(list);
            
            setArrayBets([
                [
                    { style: "grey", svg: svgMenu.Crash, langCode: "Crash" },
                    { style: "white", text: Math.random() },
                    { style: "grey", text: Math.random() },
                    { style: "white", text: Math.random() },
                    { style: "red", svg: svgCryptoPayments["btc"], text: "+210.00000" }
                ],
                ...list
            ]);
        }, 1000);

        return () => clearInterval(TODODELETETHIS)
    }, [arrayBets])

    return (
        <ContentBox titleLangCode="Last bet" svg={svgAsset.LastBet}>
            <div className={styles.content}>
                <TableRow typeRow="head" key={`bet-head`} arrayParams={headTable} />

                {
                    arrayBets.map((bet, key) =>
                        <TableRow
                            noBack={key % 2}
                            key={`bet_${key}`}
                            arrayParams={bet}
                        />
                    )
                }
            </div>
        </ContentBox>
    )
}

export default LastBet;