import { Email, Twitter, Meduim, Telegram } from '../SETTINGS';

import { svgSocial, svgAsset } from '../svgAsset';

import styles from './Social.module.scss';

const Social = () => {
    return (

        <div className={styles.right}>
            {svgAsset.Email}

            <div className={styles.email}>
                {Email}
            </div>

            <div className={styles.links}>
                <a href={Twitter} target="_blank" rel="noreferrer">{svgSocial.Twitter}</a>
                <a href={Meduim} target="_blank" rel="noreferrer">{svgSocial.Medium}</a>
                <a href={Telegram} target="_blank" rel="noreferrer">{svgSocial.Telegram}</a>
            </div>

            <a href="//www.trustpilot.com/" target="_blank" rel="noreferrer">
                {svgSocial.Trustpilot}
            </a>
        </div>
    )
}

export default Social;