import React, { useState } from 'react';

import Content from '../Content';
import Footer from '../Footer';
import Chat from '../Chat';
import Header from '../Header';
import Menu from '../Menu';
import CustomScrollbars from '../CustomScrollbars';
import ModalFirstSetting from '../ModalFirstSetting';

import styles from './index.module.scss';

const Site = () => {
    const [scrollContentTop, setScrollContentTop] = useState(0);

    const onScrollContent = ({scrollTop}) => setScrollContentTop(scrollTop)

    return (
        <>
            <div className={styles.wrapper}>
                <Menu />

                <div className={styles.content}>
                    <Header headerLikeAuth={scrollContentTop !== 0} />

                    <CustomScrollbars onUpdate={onScrollContent}>
                        <Content />
                        <Footer />
                    </CustomScrollbars>
                </div>

                <Chat />

            </div>
            
            <ModalFirstSetting />
        </>
    )
}

const SitePath = "*";

export {Site, SitePath};