import { useTranslation } from "react-i18next";
import { useState } from 'react';

import Unauth from '../ModalUnauth';

import styles from './UnauthButtons.module.scss';

const UnauthButtons = () => {
    const { t } = useTranslation();

    const [nameModal, setNameModal] = useState(false);

    return (
        <>
            <div
                className={`${styles.button} ${styles.grey}`}
                onClick={() => setNameModal('Auth')}
            >
                {t('Login')}
                </div>

            <div
                className={`${styles.button} ${styles.green}`}
                onClick={() => setNameModal('Reg')}
            >
                {t('Registration')}
            </div>

            {
                <Unauth
                    typeModal={nameModal}
                    onChange={(typeModal) => setNameModal(typeModal)}
                />
            }            
        </>
    );
};

export default UnauthButtons;