import { useState, useEffect } from 'react';

import { useTranslation } from "react-i18next";

import Button from '../CustomButton';
import CustomSelect from '../CustomSelect';
import Form from '../Modal/Form';
import WrapperModal from '../Modal/WrapperModal';

import { sessionKeys } from "../User/session";
import API from '../API';

import { svgAsset, svgFiatPayments, svgCryptoPayments } from '../svgAsset';

import styles from './AddBalance.module.scss';

const TwoFactorWindow = () => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });

    const [arrCurrency, setArrCurrency] = useState(false);
    const [keyCurrency, setKeyCurrency] = useState(false);

    const onChangeSelect = (key) => setKeyCurrency(key);

    const handlerSuccess = ({list}) => {
        let currencyArray = [];

        list.forEach(({code, name, is_optional}) => {
            if (is_optional) return;

            currencyArray.push({
                svg: Object.assign(svgFiatPayments, svgCryptoPayments)[code] || false,
                textCode: name,
                key: code,
                pre: code.toUpperCase()
            });
        });
        
        setArrCurrency(currencyArray);
    }

    const handlerSaveCurrency = () => {
        API.sendAuth(
            "balance/add",
            {
                code: keyCurrency
            },
            () => sessionKeys.update()
        );
    }

    useEffect(() => {
        !arrCurrency && API.sendAuth(
            "balance/list",
            {},
            handlerSuccess
        );
    });

    return (
        <WrapperModal
            stateOpen={true}
            classModalCustom={styles.modal}
        >

            <Form
                codeLangTitle='Wallet currency'
                textFill="Choose the main currency (You can change or add at any time)"
                svgTitle={svgAsset.Wallet}
            >

                {arrCurrency && <CustomSelect
                    selectKey={arrCurrency[0]}
                    optionList={arrCurrency}
                    onSelect={onChangeSelect}
                    customClassContainer={styles.select}
                />}

                <Button
                    isBig={true}
                    isGreen="true"
                    onClick={handlerSaveCurrency}
                >
                    {svgAsset.Save}
                    {t('Save')}
                </Button>
            </Form>
            
        </WrapperModal>
    );
}

export default TwoFactorWindow;