import { useState } from 'react';
import { useTranslation } from "react-i18next";

import CustomInput from '.';
import API from '../API';
import { Notification } from '../Notification';

import { svgAsset } from '../svgAsset';

import styles from './PhoneInput.module.scss';

const PhoneInput = ({ returnPhone, returnCode }) => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });

    const [iddValue, setIddValue] = useState(false);
    const [phoneNumberValue, setPhoneNumberValue] = useState(false);
    const [waitButton, setWaitButton] = useState(false);

    const handlerAnswer = (data) => {
        if (data.success)
            Notification.success("success.CallSend");

        if (data.wait)
            checkWait(data.wait);
    }

    const handlerSendCode = () => {
        if (!iddValue || !phoneNumberValue)
            return Notification.error("error.FillAllData");

        API.send("phone/code",
            {
                phone: iddValue + "" + phoneNumberValue
            },
            handlerAnswer,
            handlerAnswer
        );
    };

    const checkWait = async (count) => {
        if (count <= 0) return setWaitButton(false);

        setWaitButton(count);

        setTimeout(() => {
            --count;

            checkWait(count);
        }, 1000);
    }

    const buttonObj = waitButton ? {
        svg: svgAsset.Clock,
        text: `${waitButton} s`,
        disabled: true
    } : {
        svg: svgAsset.Call,
        text: t('Call'),
        callback: handlerSendCode
    };

    return (
        <>
            <div className={styles.phoneInput}>
                <CustomInput
                    customClass={styles.first}
                    langCodePlaceholder="+263"
                    inputType="IDD"
                    returnValue={(data) => {
                        setIddValue(data);

                        returnPhone("idd", data);
                    }}
                />
                
                <CustomInput
                    customClass={styles.second}
                    langCodePlaceholder="Phone Number"
                    inputType="phoneNumber"
                    returnValue={(data) => {
                        setPhoneNumberValue(data)

                        returnPhone("phone", data);
                    }}
                />
            </div>

            <CustomInput
                langCodePlaceholder="Last 4 digit number"
                inputType="code"
                returnValue={returnCode}
                configButton={buttonObj}
            />
        </>
    );
}

export default PhoneInput;