import { useState } from 'react';

import { useTranslation } from "react-i18next";

import CustomInput from '../CustomInput';
import Button from '../CustomButton';
import Form from '../Modal/Form';
import WrapperModal from '../Modal/WrapperModal';

import { sessionKeys, changeTwofa } from "../User/session";
import { Notification } from '../Notification';
import API from '../API';

import { Email } from '../SETTINGS';
import { svgAsset } from '../svgAsset';

import styles from './TwoFactorWindow.module.scss';

const TwoFactorWindow = () => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });
    const [twoFaValue, setTwoFaValue] = useState(false);

    const handlerSubmitTwoFactor = () => {
        if (!twoFaValue)
            return Notification.error("error.FillAllData");

        API.sendAuth(
            "twofactor/submit",
            {
                code: twoFaValue
            },
            () => {
                changeTwofa.disable();

                sessionKeys.update();
            }
        );
    };

    return (
        <WrapperModal
            stateOpen={true}
            classModalCustom={styles.modal}
        >
            <Form codeLangTitle='Two-factor Authentication' textFill={true} svgTitle={svgAsset.Submit}>

                <CustomInput
                    inputType="twoFactor"
                    langCodePlaceholder="Enter Twofactor Code"
                    returnValue={setTwoFaValue}
                />

                <Button
                    isBig={true}
                    isGreen="true"
                    onClick={handlerSubmitTwoFactor}
                >
                    {svgAsset.Submit}
                    {t('Submit')}
                </Button>

                <Button
                    isBig={true}
                    onClick={() => sessionKeys.clear(true)}
                >
                    {svgAsset.Exit}
                    {t('Log out')}
                </Button>

                <div className={styles.ifLost}>
                    {t('If you have lost your authenticator\n- Write to support ')}
                </div>

                <div className={styles.email}>
                    {svgAsset.Email}
                    {Email}
                </div>
            </Form>
        </WrapperModal>
    );
}

export default TwoFactorWindow;