import { stringToSha256 } from '../Util/Sha256';
import { SIGN_SECRET } from '../SETTINGS';

const bodyWithSign = (Query) => {
    Query['client'] = Date.now();

    const stringQuery = JSON.stringify(
        Object.keys(Query).sort().reduce(
            (accum, key) => {
                accum[key] = Query[key];

                return accum;
            },
            {}
        )
    );

    const hash = stringToSha256(
        stringQuery,
        SIGN_SECRET
    );

    return JSON.stringify({
        ...Query,
        sign: hash
    })
};

export default bodyWithSign;