import { svgAsset } from '../svgAsset';

import styles from './CheckBox.module.scss';

const CheckBox = ({ stateBox, boxText, stateInput, onClick }) => {
    return (
        <div
            className={
                `${styles.checkBox
                } ${stateInput ? styles.goodParent : ''
                }`
            }
            onClick={onClick}
        >
            <div className={styles.text}>
                {boxText}
            </div>

            {stateBox ? svgAsset.CheckOn : svgAsset.CheckOff}
        </div>
    );
};

export default CheckBox;