import { useState } from 'react';

import Hide from './Hide';
import Visible from './Visible';

import styles from './index.module.scss';

const Chat = () => {
    const [isChat, setIsChat] = useState(true);
    const [widthZero, setWidthZero] = useState(false);

    const toggleChat = () => {
        let isClose = false;

        if (!isChat) {
            setIsChat(current => !current);

            isClose = true;
        }

        setWidthZero(true);

        setTimeout(
            () => {
                isClose ?
                    setWidthZero(false)
                    : setIsChat(current => !current);
            },
            isClose ? 1 : 200
        );
    }

    return (
        <div className={styles.wrapper}>
            {
                isChat ?
                    <Visible zeroWidth={widthZero} onClick={toggleChat} />
                    :
                    <Hide onClick={toggleChat} />
            }
        </div>
    )
}

export default Chat;