import { useTranslation } from "react-i18next";

import Link from './Link';
import { linksMenu } from '../links';

import styles from './Links.module.scss';

const Links = () => {
    const { t } = useTranslation(null);

    return (
        <>
            {
                linksMenu.map((group, index) => (
                    <div
                        key={`group_${index}`}
                        className={`${
                            styles.links
                        } ${
                            (index > 0 && styles.line) || ''
                        }`}
                    >
                        {
                            group.map((link, index2) => (
                                <Link
                                    key={`link_${index}_${index2}`}
                                    link={link.href}
                                    text={t(link.text)}
                                    svg={link.svg}
                                />
                            ))
                        }
                    </div>
                ))
            }
        </>
    )
}

export default Links;