import { ToastContainer, toast } from 'react-toastify';

import LanguageBox from './LanguageBox';

import { svgNotification } from '../svgAsset';

import styles from './index.module.scss';

const autoCloseTime = 3000;

const Notification = ({ svg, code, langObj }) => {

    const toastId = toast(
        <>
            {svg || ''}

            <LanguageBox 
                className={styles.content} 
                codeLang={code} 
                objLang={langObj || {}} 
            />
        </>
    );

    setTimeout(() => toast.dismiss(toastId), autoCloseTime);
};

Notification.error = (codeLang, objLang = false) => Notification({
    svg: svgNotification.Error,
    code: codeLang,
    langObj: objLang
})

Notification.message = (codeLang, objLang = false) => Notification({
    svg: svgNotification.Message,
    code: codeLang,
    langObj: objLang
})

Notification.success = (codeLang, objLang = false) => Notification({
    svg: svgNotification.Success,
    code: codeLang,
    langObj: objLang
})

const ComponentNotification = () => {
    return (
        <ToastContainer
            closeButton={false}
            className={styles.container}
            toastClassName={styles.wrapper}
            bodyClassName={styles.body}
            progressClassName={styles.progress}
            autoClose={autoCloseTime}
        />
    );
}

export {
    Notification,
    ComponentNotification
};