import React from 'react';

import { Route, Routes } from "react-router";

import {SitePath, Site} from './Site';
import {PageResetPasswordPath, PageResetPassword} from './PageResetPassword';

const App = () => {
    return (
        <>
            <Routes>
                <Route path={PageResetPasswordPath} element={
                    <PageResetPassword />
                } />

                <Route path={SitePath} element={
                    <Site />
                } />
            </Routes>
        </>
    )
}

export default App;