import { useState, useRef, useEffect } from 'react';

import { useTranslation } from "react-i18next";

import CustomScrollbars from '../CustomScrollbars';

import { svgAsset } from '../svgAsset';

import styles from './index.module.scss';

const CustomSelect = ({ customClassContainer, selectKey = {}, optionList = [], isMini = false, onSelect = () => { } }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [selectedData, setSelectedData] = useState(selectKey);
    const [outClass, setOutClass] = useState('');
    const wrapperRef = useRef(null);

    useOutsideAlerter(wrapperRef, () => handlerClose());

    const handlerSelect = (key) => {
        onSelect(key);

        setSelectedData(searchByKey(key));
        handlerClose()
    }

    const searchByKey = (value) => optionList.find(({ key }) => key === value);
    
    const handlerClose = () => {
        setOutClass(styles.out);

        setTimeout(() => {
            setIsOpen(false);

            setOutClass('');
        }, 100);
    }

    const toggleDrop = () => isOpen ? handlerClose() : setIsOpen(true);

    useEffect(() => onSelect(selectedData.key));

    return (<>
        <div
            className={`${styles.container
                } ${customClassContainer || ''
                } ${isMini ? styles.mini : styles.big
                }  ${isOpen ? styles.open : ''
                }`}
            ref={wrapperRef}
        >
            <div className={styles.selected} onClick={toggleDrop}>
                {selectedData && <Content inObj={selectedData} key={"option-selected"} />}

                <div className={styles.arrow}>{svgAsset.ArrowDown}</div>
            </div>

            {isOpen && (
                <div className={`${styles.list} ${outClass}`}>
                    <CustomScrollbars verticalZero={true}>
                        {optionList.map((option) => (
                            <div
                                className={styles.option}
                                onClick={() => handlerSelect(option.key)}
                                key={`option-${option.key}`}
                            >
                                <Content inObj={option} />
                            </div>
                        ))}
                    </CustomScrollbars>
                </div>
            )}
        </div>
    </>);
}

const Content = ({ inObj }) => {
    const { svg, textCode, text, preCode, pre } = inObj;

    return (
        <>
            {svg || ''}

            <Text langCode={textCode} staticText={text} />

            <Text isPre={true} langCode={preCode} staticText={pre} />
        </>
    )
}

const Text = ({ isPre, langCode, staticText }) => {
    const { t } = useTranslation(null, { keyPrefix: "select" });

    return (
        (langCode || staticText) &&

        <div className={isPre ? styles.pre : styles.text}>
            {langCode && t(langCode)}
            {staticText && staticText}
        </div>
    )
}

const useOutsideAlerter = (ref, callbackClick) => {
    useEffect(() => {
        function handleClickOutside(event) {
            if (ref.current && !ref.current.contains(event.target)) {
                callbackClick();
            }
        }

        document.addEventListener("mousedown", handleClickOutside);

        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [ref, callbackClick]);
}

export default CustomSelect;