import styles from './index.module.scss';

const Button = ({
    classNameCustom,
    isGreen,
    isBig,
    children,
    onClick = () => { }
}) => {

    const classNames = [
        styles.button,
        isGreen ? styles.green : false,
        isBig ? styles.big : false,
        classNameCustom || false,
    ].filter(element => element !== false);

    return (
        <div className={classNames.join(' ')} onClick={onClick}>
            {children}
        </div>
    );
}

export default Button;