import { useState, useEffect, useCallback } from 'react';

import { SingleBanner, ListBanners } from './ConfigBanners';
import { svgAsset } from '../svgAsset';
import ImageWrapper from './ImageWrapper';
import Points from './Points';

import styles from './index.module.scss';

const autoChangeTime = 3000;

const TopBanners = () => {
    const [indexBanner, setIndexBanner] = useState(0);
    const [blinkBanner, setBlinkBanner] = useState(0);

    const changeIndex = useCallback((calc = 1) => {
        const lastIndexBanner = indexBanner;
        let newIndex = lastIndexBanner + calc;
        
        if (newIndex >= ListBanners.length) newIndex = 0;
        if (newIndex < 0) newIndex = ListBanners.length - 1;

        setIndexBanner(newIndex);

        setTimeout(() => setBlinkBanner(newIndex), 500);
    }, [
        indexBanner, 
        setIndexBanner
    ]);

    useEffect(() => {
        let timer =  setInterval(changeIndex, autoChangeTime);
        
        return () => {
            clearInterval(timer);
        };
    }, [
        changeIndex
    ]);

    return (
        <div className={styles.wrapper}>
            <div className={`${styles.block}`}>
                <ImageWrapper settingsObj={ListBanners[blinkBanner]} />
                <ImageWrapper settingsObj={ListBanners[indexBanner]} />
                
                <div className={styles.slide} 
                    onClick={() => changeIndex(-1)}
                >{svgAsset.ArrowInBoxLeft}</div>
                <div className={`${styles.slide} ${styles.right}`} 
                    onClick={()=>changeIndex()} 
                >{svgAsset.ArrowInBoxLeft}</div>

                <Points 
                    activePoint={indexBanner + 1} 
                    count={ListBanners.length} 
                    changeOnClick={(numPos)=>setIndexBanner(numPos-1)}
                />
            </div>

            <div className={`${styles.block}`}>
                <ImageWrapper settingsObj={SingleBanner} />
            </div>
        </div>
    )
}

export default TopBanners;