import { useTranslation } from "react-i18next";

import CustomScrollbars from '../CustomScrollbars';
import LanguageButton from './LanguageButton';
import CloseRound from '../Modal/CloseRound';
import WrapperModal from '../Modal/WrapperModal';

import resources from "../Language/resources";

import styles from './index.module.scss';

const ModalLanguage = ({ isOpen, onClose }) => {
    const { t } = useTranslation(null, { keyPrefix: 'modal.title' });

    return (
        <WrapperModal
            stateOpen={isOpen}
            classModalCustom={styles.modal}
            onClose={onClose}
        >
            <div className={styles.title}>{t('Language')}</div>

            <CloseRound closeClick={onClose} customClass={styles.close}/>

            <div className={styles.list}>
                <CustomScrollbars>
                    <div className={styles.grid}>
                        {
                            Object.entries(resources).map(([code, lang], index) => (
                                <LanguageButton
                                    key={`language_${index}`}
                                    svg={lang.svg}
                                    text={lang.name}
                                    codeName={code}
                                />
                            ))
                        }
                    </div>
                </CustomScrollbars>
            </div>
        </WrapperModal>
    );
}

export default ModalLanguage;