import { NavLink } from "react-router-dom";

import StyleBackgroundImage from "../StyleBackgroundImage";
import styles from './ImageWrapper.module.scss';

const ImageWrapper = ({settingsObj}) => {
    const { img, link, imgHover } = settingsObj;

    const block =
        <StyleBackgroundImage
            className={styles.img}
            backgroundImage={img}
        >
            {
                imgHover && <StyleBackgroundImage
                    className={`${styles.img} ${styles.hover}`}
                    backgroundImage={imgHover} 
                    />
            }
        </StyleBackgroundImage>

    return link ? <NavLink
        to={link}
        target="_blank"
    >
        {block}
    </NavLink> : block;
}

export default ImageWrapper;