import { useTranslation } from "react-i18next";
import { NavLink } from "react-router-dom";
import { observer } from "mobx-react-lite"

import UnauthButtons from './UnauthButtons';
import ContentWrapper from "../ContentWrapper";

import {linksHeader} from '../links';
import storeUser from "../store/user";

import {svgAsset} from '../svgAsset';

import styles from './index.module.scss';

const Header = observer(({headerLikeAuth}) => {
    const { t } = useTranslation();

    return (
        <ContentWrapper 
            customClassContent={styles.content} 
            customClassWrapper={`${styles.wrapper} ${(storeUser.isAuth || headerLikeAuth ? styles.isAuth : '')}`}
        >
                <NavLink 
                    strict="true" 
                    to="/" 
                    end="true" 
                    className={styles.logo}
                >
                    {svgAsset.Logo}
                </NavLink>

                <div className={styles.links}>
                {
                    linksHeader.map((link, index) => (
                        <NavLink 
                            key={`navlinkHead_${index}`}
                            strict="true"
                            to={link.href}
                            className={styles.link} 
                            end="true"
                        >
                            {t(link.text)}
                        </NavLink>
                     ))
                }
                </div>
                
                {
                    storeUser.isAuth ? '' : <UnauthButtons />
                }
        </ContentWrapper>
    )
});

export default Header;