import { useTranslation } from "react-i18next";

import {svgSocial} from '../svgAsset';

import styles from './LoginDirectly.module.scss';

const LoginDirectly = () => {
    const { t } = useTranslation(null, {keyPrefix: "modal"});

    return (

        <div className={styles.loginDirectly}>
            {t('Log in directly with')}

            <span>{svgSocial.colorMetamask}</span>
            <span>{svgSocial.colorWalletConnect}</span>
            <span>{svgSocial.colorTelegram}</span>
            <span>{svgSocial.colorSteam}</span>
        </div>
    );
}

export default LoginDirectly;