import { useTranslation } from "react-i18next";

const LanguageBox = ({className, codeLang, objLang = {}}) => {
    const { t } = useTranslation(null, { keyPrefix: 'notification' });

    return (
        <div className={className}>{t(codeLang, objLang)}</div>
    );
}

export default LanguageBox;