import React from "react";

import { Scrollbars } from 'react-custom-scrollbars-2';

import styles from './index.module.scss';

class CustomScrollbars extends React.Component {
  render() {
    const styleVertical = this.props.verticalZero ? styles.zeroVerticalPos : styles.verticalPos;
    const styleHorizontal = this.props.horizontalZero ? styles.zeroHorizontalPos : styles.horizontalPos;

    return (
      <Scrollbars 
        renderTrackHorizontal={props => <div {...props} className={`${styles.horizontalTrack} ${styleHorizontal}`}/>}
        renderTrackVertical={props => <div {...props} className={`${styles.verticalTrack} ${styleVertical}`}/>}
        renderThumbHorizontal={props => <div {...props} className={`${styles.thumb} ${styles.horizontal}`}/>}
        renderThumbVertical={props => <div {...props} className={`${styles.thumb} ${styles.vertical}`}/>}
        {...this.props}
      >
        {this.props.children}
      </Scrollbars>
    );
  }
}

export default CustomScrollbars;