import bodyWithSign from './bodyWithSign';
import prepeareCallback from './prepeareCallback';
import { sessionKeys } from "../User/session";
import { API_URL } from '../SETTINGS';

const API = async (option) => {
    const { query, method, Auth, callback } = option;
    const headers = Object.assign(
        {
            "content-type": "application/json",
            "accept": "application/json"
        },
        Auth ? {
            "X-W-Auth": sessionKeys.read()
        } : {}
    );

    if (query)
        query.language = localStorage.getItem("W-Language");

    fetch(API_URL + "/" + method, {
        "method": "POST",
        "headers": headers,
        "body": bodyWithSign(query)
    })
        .then(response => response.json())
        .then(response => {
            prepeareCallback(callback, response);
        })
        .catch(err => {
            console.log("REQ ERROR");
            console.log(err);

            prepeareCallback(callback, {
                success: false,
                code: "error.CantRequest"
            });
        });
}

API.sendAuth = (path, query, callbackSuccess = null, callbackError = null) => {
    API({
        method: path,
        query: query,
        Auth: true,
        callback: {
            error: callbackError,
            success: callbackSuccess
        }
    });
}

API.send = (path, query, callbackSuccess = null, callbackError = null) => {
    API({
        method: path,
        query: query,
        Auth: false,
        callback: {
            error: callbackError,
            success: callbackSuccess
        }
    });
}

export default API;