import ContentBox from '../ContentBox';
import {svgAsset} from '../svgAsset';

import ConfigGames from './ConfigGames';
import BlockGame from './BlockGame';

import styles from './index.module.scss';

const IntroGames = () => {
    return (
        <ContentBox titleLangCode="Games" svg={svgAsset.Games} customClassContent={styles.content}> 
            {
                ConfigGames.map((game, index) => <BlockGame key={`game_${index}`} settingsObj={game} />)
            }
        </ContentBox>
    )
}

export default IntroGames;