import Blowfish from "egoroof-blowfish";
import { Buffer } from 'buffer';

const decodeBlowfish = (key, str) => {
    const bf = new Blowfish(key, Blowfish.MODE.ECB, Blowfish.PADDING.NULL);

    const uint8arr = Buffer.from(str, 'hex');

    return bf.decode(uint8arr, Blowfish.TYPE.STRING)
};

const encodeBlowfish = (key, str) => {
    const bf = new Blowfish(key, Blowfish.MODE.ECB, Blowfish.PADDING.NULL);

    const uint8arr = bf.encode(str);

    return Buffer.from(uint8arr).toString('hex');
};

export {
    encodeBlowfish,
    decodeBlowfish,
}