import WrapperModal from '../Modal/WrapperModal';
import BlockAuth from './BlockAuth';
import BlockReg from './BlockReg';
import BlockReset from './BlockReset';
import BlockResetSuccess from './BlockResetSuccess';
import Wrapper from './Wrapper';
import Head from './Head';
import LoginDirectly from './LoginDirectly';

const ModalUnauth = ({ typeModal, onChange }) => {
    const blocks = {
        Auth: <BlockAuth changeType={onChange} />,
        Reset: <BlockReset changeType={onChange} />,
        Reg: <BlockReg changeType={onChange} />,
        BlockResetSuccess: <BlockResetSuccess changeType={onChange} />
    };

    const handlerClose = () => onChange(false);
    
    return (
        <WrapperModal
            stateOpen={typeModal ? true : false}
            onClose={handlerClose}
        >
            <Head closeClick={handlerClose} />

            <Wrapper>
                { blocks[typeModal] }

                <LoginDirectly />
            </Wrapper>
        </WrapperModal>
    );
}

export default ModalUnauth;