import styles from './Button.module.scss';

const Button = ({ children, classDisabled, onClick }) => {
    return (
        <div
            className={
                `${
                    styles.button
                } ${
                    classDisabled ? styles.disabled : ''
                }`
            }
            onClick={onClick}
        >
            {children}
        </div>
    );
};

export default Button;