import { useTranslation } from "react-i18next";
import { observer } from "mobx-react-lite"

import { svgAsset } from '../svgAsset';
import API from '../API';
import User from "../store/user";

import styles from './LanguageButton.module.scss';

const LanguageButton = observer(({ svg, text, codeName }) => {
    const { t, i18n } = useTranslation();

    const handleLangChange = (code) => {
        if (t('type') === code) return;
        
        if (!User.isAuth) return i18n.changeLanguage(code);

        API.sendAuth("language/change", {
            code: code
        }, () => {
            i18n.changeLanguage(code);
        });
    }

    return (
        <div
            className={`${styles.button
                } ${(codeName === t('type') && styles.selected) || ''
                }`}
            onClick={() => handleLangChange(codeName)}
        >
            <div className={styles.flag}>
                {svg}
            </div>

            <div className={styles.name}>
                {text}
            </div>

            {
                codeName === t('type') && (
                    <div className={styles.tick}>
                        {svgAsset.Tick}
                    </div>
                )
            }
        </div>
    );
})

export default LanguageButton;