import { observer } from "mobx-react-lite"

import TwoFactorWindow from "./TwoFactorWindow";
import AddBalance from "./AddBalance";

import Twofactor from "../store/twofactor";
import User from "../store/user";

const ModalFirstSetting = observer(() => {
    return (
        <>
            {Twofactor.isNeed && <TwoFactorWindow />}
            {User.isAuth && User.countBalance() === 0 && <AddBalance />}
        </>
    )
});

export default ModalFirstSetting;