import { useEffect } from "react";

import { useTranslation } from "react-i18next";

import { sessionKeys } from './User/session';

const InitOnLoad = () => {
  const { i18n } = useTranslation();

  useEffect(() => {
    baseUser();

    listenStorage(i18n);

    setTimeout(killPreload, 1000); 
  }, [i18n]);
};

const killPreload = () => document.getElementById("preloader").remove();

const baseUser = () => localStorage.getItem('W-Auth') && sessionKeys.check();

const listenStorage = (i18nV) => {
  window.addEventListener('storage', ({key}) => {
    switch (key) {

      case 'W-Language':
          i18nV.changeLanguage(localStorage.getItem('W-Language'));
        break;

      case 'W-Auth':
          sessionKeys.check(500);
        break;

      default:
    }
  })
}

export default InitOnLoad;