import { useTranslation } from "react-i18next";

import TabSwitch from './TabSwitch';

import {svgAsset} from '../svgAsset';

import styles from './TopSwitch.module.scss';

const TopSwitch = ({ onClickTab, switchType }) => {
    const { t } = useTranslation(null, { keyPrefix: 'modal' });

    return (
        <div className={styles.topSwitch}>
            <TabSwitch active={switchType} tabType='Phone' onClick={onClickTab}>
                {svgAsset.Phone}
                {t('Phone')}
            </TabSwitch>

            <TabSwitch active={switchType} tabType='Email' onClick={onClickTab}>
                {svgAsset.Email}
                {t('Email')}
            </TabSwitch>
        </div>
    );
}

export default TopSwitch;