import { NavLink } from "react-router-dom";

import StyleBackgroundImage from "../StyleBackgroundImage";

import styles from './BlockGame.module.scss';

const BlockGame = ({ settingsObj }) => {
    const { img, link, imgHover} = settingsObj;

    return (
        <NavLink to={link}>
            <StyleBackgroundImage
                className={styles.img}
                backgroundImage={img}
            >
                {
                    imgHover && <StyleBackgroundImage
                        className={`${styles.img} ${styles.hover}`}
                        backgroundImage={imgHover}
                    />
                }
            </StyleBackgroundImage>
        </NavLink>
    );
}


export default BlockGame;