import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import styles from './index.module.scss';

const TableRow = ({ arrayParams, noBack = false, typeRow = "row" }) => {
    const [animationAdd, setAnimationAdd] = useState(true);

    const classNamesRow = typeRow === 'head' ? [
        styles.rowHead
    ] : [
        styles.row,
        (noBack && styles.colorBack),
        animationAdd && styles.animationOnAdd
    ];

    useEffect(()=>{
        setTimeout(() => setAnimationAdd(false), 500);
    })


    return <div 
        className={filterClassName(classNamesRow)}
        style={{gridTemplateColumns : `0.7fr repeat(${arrayParams.length-2}, 1fr) 0.7fr`}}
        >
        {
            arrayParams.map((props, key) =>
                <Column
                    isHead={typeRow === "head"}
                    key={`row_${key}`}
                    {...props}
                />
            )
        }
    </div>
}

const Column = ({ langCode, isHead, svg, style = "", text, element }) => {
    const { t } = useTranslation(null);

    const classNames = [
        styles.column,
        (isHead && styles.head),
        (style.includes("green") && styles.green),
        (style.includes("white") && styles.white),
        (style.includes("grey") && styles.grey),
        (style.includes("red") && styles.red),
        (style.includes("center") && styles.center)
    ]

    return <div
        className={filterClassName(classNames)}
    >
        {svg || ''}
        {(langCode && t(langCode)) || ''}
        {text || ''}
        {element || ''}
    </div>
};

const filterClassName = (arrClasses) => arrClasses.filter(x => x).join(' ');

export default TableRow;