import React from 'react';

import {svgCryptoPayments, svgFiatPayments} from '../svgAsset';

import styles from './PaymentIcons.module.scss';

function PaymentIcons() {
   return (
        <div className={styles.wrapper}>
            <div className={styles.icons}>
            {                
                Object.values(svgCryptoPayments).map((icon, index) => <React.Fragment key={index}>{icon}</React.Fragment>)
            }
            </div>

            <div className={`${styles.right} ${styles.icons}`}>
            {                
                Object.values(svgFiatPayments).map((icon, index) => <React.Fragment key={index}>{icon}</React.Fragment>)
            }
            </div>
        </div>
    )
}

export default PaymentIcons;