import { useEffect, useState, useCallback } from 'react';

import Modal from 'react-modal';

import styles from './WrapperModal.module.scss';

Modal.setAppElement("#root");

const WrapperModal = ({ stateOpen, onClose = false, classModalCustom, children }) => {
    const [outClass, setOutClass] = useState('');
    const [isModalOpen, setIsModalOpen] = useState(stateOpen);

    const handlerClose = useCallback(() => {
        if (typeof onClose !== 'function') return;

        setOutClass(styles.out);

        setTimeout(() => {
            onClose();

            setIsModalOpen(false);

            setOutClass('');
        }, 200);
    }, [onClose]);

    useEffect(() => {
        if (isModalOpen !== stateOpen) {
            if (stateOpen === true) {
                setIsModalOpen(true);
            } else {
                handlerClose();
            }
        }
    }, [isModalOpen, stateOpen, handlerClose]);

    return (
        <Modal
            isOpen={isModalOpen}
            className={`${classModalCustom || ''} ${styles.modal} ${outClass}`}
            onRequestClose={handlerClose}
            overlayClassName={styles.overlay}
        >
            {children}
        </Modal>
    )
}

export default WrapperModal;