import styles from './TabSwitch.module.scss';

const TabSwitch = ({tabType, active, children, onClick}) => {
    return (        
        <div 
            className={`${styles.tabSwitch} ${tabType === active ? styles.active :''}`}
            onClick={() => onClick(tabType) }
        >
            {children}
        </div>
    );
}

export default TabSwitch;