import { useTranslation } from "react-i18next";

const StyleBackgroundImage = ({children, backgroundImage, ...props}) => {
    const { t } = useTranslation(null);

    return <div
        style={{
            backgroundImage: `url(${
                    process.env.PUBLIC_URL
                }/images/${
                    backgroundImage.replace("{lang}", t('type'))
                })`
        }}
        
        {...props}
    >
        {children}
    </div>
}

export default StyleBackgroundImage;