import { useTranslation } from "react-i18next";

import { svgAsset } from '../svgAsset';

import styles from './index.module.scss';

const ContentBox = ({
    customClassBox = "",
    customClassContent = "",
    titleLangCode = "",
    svg = "",
    children,
    onArrowClick
}) => {
    const { t } = useTranslation();

    return (
        <div className={`${styles.wrapper} ${customClassBox}`}>
            {
                typeof onArrowClick === 'function' && <div className={styles.arrows}>
                    <div
                        className={styles.arrow}
                        onClick={() => onArrowClick('left')}
                    >
                        {svgAsset.ArrowInBoxLeft}
                    </div>
                    <div
                        className={`${styles.arrow} ${styles.right}`}
                        onClick={() => onArrowClick('right')}
                    >
                        {svgAsset.ArrowInBoxLeft}
                    </div>
                </div>
            }

            <div className={styles.title}>
                {svg}

                {t(titleLangCode)}
            </div>

            <div className={`${styles.content} ${customClassContent}`}>
                {children}
            </div>
        </div>
    )
}

export default ContentBox;