import { useState } from 'react';

import { useParams, Navigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

import CustomInput from '../CustomInput';
import Button from '../CustomButton';
import Form from '../Modal/Form';
import {Notification} from '../Notification';
import API from '../API';
import { svgAsset } from '../svgAsset';
import { stringToSha256 } from '../Util/Sha256';

import styles from './index.module.scss';

const PageResetPassword = () => {
    let { authId, resetHash } = useParams();
    const { t } = useTranslation("modal");

    const [redirectToIndex, setRedirectToIndex] = useState('');

    const [passwordValue, setPasswordValue] = useState(false);
    const [passwordRepValue, setPasswordRepValue] = useState(false);

    const handlerSuccess = () => setRedirectToIndex(<Navigate to="/" />);

    const handlerReset = () => {
        if (!passwordValue || !passwordRepValue) {
            return Notification.error("error.FillAllData");
        }

        if (passwordRepValue !== passwordValue) {
            return Notification.error("error.PasswordNotSame");
        }

        const passwordHash = stringToSha256(passwordValue);

        API.send("resert/submit",
            {
                id: authId,
                resert: resetHash,
                password: passwordHash
            },
            handlerSuccess
        );
    };

    return (
        <div className={styles.wrapper}>
            {redirectToIndex}

            <div className={styles.content}>
                <div 
                    className={styles.logo} 
                    onClick={handlerSuccess}
                >
                    {svgAsset.Logo}
                </div>

                <Form textFill={true} codeLangTitle="Reset" classForm={styles.form}>
                        <CustomInput
                            inputType="password"
                            langCodePlaceholder="Password"
                            returnValue={(data) => setPasswordValue(data)} 
                        />

                        <CustomInput
                            inputType="repPassword"
                            langCodePlaceholder="Repeat Password"
                            returnValue={(data) => setPasswordRepValue(data)} 
                        />

                        <Button
                            isBig="true"
                            isGreen="true"
                            onClick={handlerReset}
                        >
                            {svgAsset.Key}
                            {t('Reset password')}
                        </Button>
                    </Form>
            </div>
        </div>
    )
}

const PageResetPasswordPath = "/ResetPassword/:authId/:resetHash";

export { PageResetPassword, PageResetPasswordPath};